
import Search from 'vux/src/components/search/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


import moduleTitle from '../../components/ModuleTitle';
import searchResult from './components/SearchResult';
import info from './components/Info';
export default {
name:'knowledge',
components:{moduleTitle,Search,Toast,searchResult,info},
data(){
return {
searchText:'',
showToast:false,
toastMsg:'',
autoFixed:false,
text:'',
searchArr:[],
isResultShow:true,
isInfoShow:false,
closePng:false,
closePngText:false
}
},

methods:{
onSubmit (e) {

let theEvent = e || window.event;
let code = theEvent.keyCode || theEvent.which || theEvent.charCode;
console.log(code);
if ((!code == 13) || (!this.searchText)){
return false;
}
if(!this.searchText){
return false;
}
e.preventDefault();
e.target.blur();
var vm = this;

this.$http.post('/api/knowledge/getByKeywords',{str:this.searchText}).then((result) => {
let data = result.data;
vm.isResultShow = true;
vm.isInfoShow = false;
if(data.successful && (data.status==200) && data.resultCode.message=='getByKeyWords successfully.'){
if(data.data){
vm.searchArr = JSON.parse(data.data);
}
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
});
return false;
},
onCancel () {
console.log('on cancel')
},
onFocus () {
console.log('on focus')
},
showResult(msg){
console.log('111',msg);
if(msg.key == 'isResultShow'){
this.isResultShow = msg.value;
this.isInfoShow = !msg.value;
}else{
this.isInfoShow = msg.value;
this.isResultShow = !msg.value;
}
},
clickSubmit(uri,val){
this.onSubmit (val);
},
showClose(){
this.closePng = !this.closePng;
},
emptyText(){
document.querySelectorAll('.searchBoxInput')[0].focus();
this.closePng = true;
this.searchText = '';
this.closePngText = false;
},
emptyAndBlurText(){
this.searchText = '';
this.closePng = !this.closePng;
this.searchArr = [];
this.isResultShow = false;
this.closePngText = false;
}
},
watch:{
searchText(n,o){
if(n){
this.closePngText = true;
}else{
this.closePngText = false;
}
}
}
}



