
import global from '../assets/globalEvent.js';
import Toast from 'vux/src/components/toast/index.vue'

export default {
name:'knowledgeInfo',
components:{Toast},
props:['isShow'],
data(){
return {
infoObj:{},
showToast:false,
toastMsg:''
}
},
mounted(){
var vm = this;
global.$on('uri',function(uri){

this.$http.post('/api/knowledge/getByUrl',{url:uri}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode.message=='getByURI successfully.'){
if(data.data){
vm.infoObj = JSON.parse(data.data);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
});
},
methods:{
toParent(uri){
return false;
var vm = this;

this.$http.post('/api/knowledge/getByUrl',{url:uri}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode.message=='getByURI successfully.'){
if(data.data){
vm.infoObj = JSON.parse(data.data);
}
}else{
vm.showToast = true;
vm.toastMsg = '查询失败';
console.log(data.resultCode);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '查询失败';
});
}
}
}



