import global from '../assets/globalEvent.js';
export default {
    name:'searchResult',
    props:['showArr','isShow'],
    methods:{
        showInfo(uri){
            if(uri){
                global.$emit('uri',uri);
                this.$emit('listenShow',{key:'isResultShow',value:false});
            }
        }
    }
}



